(function() {
  'use strict';

  angular
    .module('app', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('app')
    .directive('navbar', navbar);

  /** @ngInject */
  function navbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html'
    };

    return directive;

  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;

    vm.pageList = [
      {page:1, description:"This is a page", url: "page1"},
      {page:2, description:"This is a page", url: "page2"},
      {page:3, description:"This is a page", url: "page3"}

    ]

  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('page1', {
        url: '/1',
        templateUrl: 'app/pages/page.1.html'
      })
      .state('page2', {
        url: '/2',
        templateUrl: 'app/pages/page.2.html'
      })
      .state('page3', {
        url: '/3',
        templateUrl: 'app/pages/page.3.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=row><div class=\"col-sm-6 col-md-4\" ng-repeat=\"list in main.pageList\"><div class=thumbnail><div class=caption><h3>Page {{ list.page }}</h3><p>{{ list.description }}</p><p><a ui-sref=\"{{ list.url }}\">Check it out</a></p></div></div></div></div>");
$templateCache.put("app/pages/page.1.html","<h1>Page 1<h1><span><a ng-href=#>Back to Home</a></span><p>Astronomy compels the soul to look upward, and leads us from this world to another.</p></h1></h1>");
$templateCache.put("app/pages/page.2.html","<h1>Page 2<h1><span><a ng-href=#>Back to Home</a></span><p>Astronomy compels the soul to look upward, and leads us from this world to another.</p></h1></h1>");
$templateCache.put("app/pages/page.3.html","<h1>Page 3<h1><span><a ng-href=#>Back to Home</a></span><p>Astronomy compels the soul to look upward, and leads us from this world to another.</p></h1></h1>");
$templateCache.put("app/components/navbar/navbar.html","<nav class=\"navbar navbar-static-top navbar-inverse\"><div class=container-fluid><div class=navbar-header><a class=navbar-brand ng-href=#><span class=\"glyphicon glyphicon-home\"></span> YoNgBoiler</a></div></div></nav>");}]);